export default {
	data() {
		return {

		};
	},
	props: {
		CouponListItem: Object
	},
	methods: {
		addCoupon(couponId, goodsName) {
			uni.showModal({
				content: `是否领取${goodsName}商品优惠卷`,
				success: (res) => {
					if (res.confirm) {
						this.$api.addCouponByUser({
							couponId: couponId
						}, (res) => {
							if (res.code) {
								this.$common.successToShow("领取优惠卷成功")
							} else {
								this.$common.errorToShow(res.data)
							}
						})
					}
				}
			});
		}
	}
}
